body {
  background-color: rgb(10, 10, 10);
  margin: 0;
  overflow-x: hidden;
  user-select: none;
  transition: 0.5s;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: rgba(6, 6, 6, 0);
}

::-webkit-scrollbar-thumb {
  border-radius: 0.3vw;
  background: linear-gradient(to bottom,
  rgb(40, 40, 40), rgb(35, 35, 35), rgb(30, 30, 30), 
  rgb(25, 25, 25), rgb(20, 20, 20));
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to bottom,
  rgb(45, 45, 45), rgb(40, 40, 40), rgb(35, 35, 35), 
  rgb(30, 30, 30), rgb(25, 25, 25));
}

p {
  color: rgb(240, 240, 240);
}

a {
  text-decoration: none;
  cursor: pointer;
  color: rgb(180, 180, 180);
  transition: all 0.2s;
}

a:hover {
  text-decoration: none;
  color: rgb(140, 140, 140);
}

button {
  transition: 0.2s;
  outline: none;
  border: none;
  padding: 0;
}

button:hover {
  transform: translate(0, -1px);
  transition: 0.3s;
}

.hint {
  display: block;
  position: fixed;
  left: 50%;
  bottom: 50px;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  border: none;
  border-radius: 10px;
  background-color: rgba(30, 30, 30, 0.8);
  backdrop-filter: blur(20px);
  box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 120%;
  opacity: 0;
  z-index: 6;
  transition: 0.2s;
}

.hint.show {
  bottom: 100px;
  opacity: 1;
  z-index: 6;
  transition: 0.3s;
}

.PageShadow {
  position: relative;
  left: -25%;
  width: 150%;
  box-shadow: 0px 0px 2vh 2vh black;
  z-index: 3;
}

.TopPanel {
